import React,{ Component } from 'react';
import {connect} from 'react-redux';
import style from '../public/css/home.scss';
import PropTypes from 'prop-types';
import {PostAvatar} from '../common/index';


class Main extends Component {
    constructor (props) {
        super(props);
        App.getNextData(this,{
            header: {
                content: '主页',
                show: true,
                left: {
                    content: '登录/注册',
                    src: '/signIn',
                    avatar: true
                },
                right: {
                    icons: 'newPost_icon.png',
                    src: '/postEdit'
                }
            },
            search: {
                show: true
            }
        });
        this.state = {
            loadDataState: 0,
            page: 1
        }
    }
    render () {
        const {posts} = this.props;
        const {loadDataState} = this.state;
        let loadingImg = {
            background: `url(${require('../public/img/loading_icon.gif')}) no-repeat`,
            backgroundSize: '0.5rem 0.5rem',
            backgroundPosition: '0 0'
        };
        let loadMessage = '';
        if (loadDataState === 2) {
            loadingImg = {};
            loadMessage = '加载完毕'
        }
        return (
            <div className={style.home}>
                <ul ref={(ul) => this.page = ul} className={style.list}>
                    {posts.map((post) =>
                        <Topic {...post} key={post._id} />
                    )}
                </ul>
                {!!loadDataState && <span className={style.loading} style={loadingImg} >{loadMessage}</span>}
            </div>
        )
    }
    componentDidMount () {
        this.onScrollBottom();
    }
    onScrollBottom () {
        window.onload = () => {
            const clientHeight = parseFloat(document.documentElement.clientHeight);
            setTimeout(() => {
                this.offset = parseFloat(window.getComputedStyle(App.DOM).height) - 1;
            }, 100);
            window.onscroll = () => {
                let srcoll = document.documentElement.scrollTop || window.pageYOffset || document.body.scrollTop;
                if (clientHeight + srcoll >= this.offset && this.offset > 1000) {
                    if (this.state.loadDataState === 0) {
                        this.setState({
                            loadDataState: 1
                        });
                        this.loadData();
                    }
                }
            }
        }
    }
    loadData () {
        const data = {
            page: ++this.state.page
        };
        App.api.post('/post/init', data).then((res) => {
            if (res.code === 0) {
                const {postAdd} = this.props;
                setTimeout(() => {
                    let state = 0;
                    const data = res.data;
                    if (data.length === 0) {
                        state = 2;
                    }
                    this.setState({
                        loadDataState: state
                    });
                    postAdd(res.data);
                    this.offset = parseFloat(window.getComputedStyle(App.DOM).height) - 1;
                }, 500)
            }
        })
    }
}


class Topic extends Component{
    render () {
        const {author, createAt, _id, good, title} = this.props;
        return (
            <li className={style.topic} onClick={this.navigateTo.bind(this, _id)}>
                <PostAvatar author={author} createAt={createAt} />
                <h3>{title}</h3>
                {/*<p></p>*/}
                <div className={style.commentWrap}>
                    <span>{good}点赞</span>
                    <span className={style.comment}>{good}评论</span>
                </div>
            </li>
        )
    }
    navigateTo (id) {
        this.context.router.history.push(`/postDetail/${id}`);
    }
}
Topic.contextTypes = {
    router: PropTypes.object.isRequired
};

const mapStateToProps = (state) => ({
    posts: state.posts,
    user: state.user,
    path: state.path
});
function mapDispatchToProps(dispatch, ownProps) {
    return {
        pageChange (path) {
            dispatch({
                type: 'changePage',
                path
            })
        },
        postAdd (data) {
            dispatch({
                type: 'postAdd',
                data
            })
        }
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(Main)