import {createStore, combineReducers, applyMiddleware} from 'redux';
import thunk from 'redux-thunk'

function posts(posts = [], action) {
    switch (action.type) {
        case 'createPost':
            return [action.post , ...posts];
        case 'postInit':
            return action.posts;
        case 'postAdd':
            return [...posts, ...action.data]
        default:
            return posts
    }
}

function user(user = null, action) {
    switch (action.type) {
        case 'userInit':
            return action.user || null;
        default:
            return user
    }
}

function prompt(prompt = {status: 1}, action) {
    switch (action.type) {
        case 'prompt':
            delete action.type;
            action.status = 0;
            return action;
        case 'removePrompt':
            return {status: 1};
        default:
            return prompt
    }
}

function path(path = {
    init: {
        side: {
            show: false
        },
        header: {
            show: false,
            content: '',
            left: {
                back: false,
                content: '',
                src: ''
            },
            right: {
                content: '',
                src: '',
                notSelect_icons: '',
                icons: '',
                callback: ''
            }
        },
        search: {
            show: false,
            state: 0
        }
    }
}, action) {
    switch (action.type) {
        case 'changePage':
            path[action.path.path] = action.path;
            return Object.assign({}, path);
        case 'propertyChange':
            const property = action.property.split(',');
            const pathName = action.pathName;
            let finalProperty = path[pathName];
            for (let key of property) {
                finalProperty = finalProperty[key]
            };
            finalProperty = '1'
            return path;
        default:
            return path;
    }
}

const store = createStore(combineReducers({posts, user, prompt, path}), applyMiddleware(thunk));

export default store